<template>
  <v-layout justify-center row class="pt-2">
    <v-img
      :src="src"
      :max-width="width"
    />
  </v-layout>
</template>

<script>
export default {
  data() {
    return {
      src: 'https://cdn.buttercms.com/PGJPyIwaQ2KnOA8UyKfH',
    };
  },
  computed: {
    width() {
      if (this.$vuetify.breakpoint.mdAndUp) return 256;
      else return 168;
    },
  },
};
</script>
